@import 'variables'

@font-face
  font-family: 'VT323'
  src: url('../../assets/fonts/vt323.woff2') format('woff2'),
  src: url('../../assets/fonts/vt323.woff') format('woff')
  font-weight: normal
  font-style: normal

*
  line-height: 60%

html
  position: relative
  min-height: 99vh

  &::before

    content: " "
    display: block
    position: absolute
    top: 0
    left: 0
    bottom: 0
    right: 0
    background: linear-gradient(rgba(18, 16, 16, 0) 50%, rgba(0, 0, 0, 0.25) 50%), linear-gradient(90deg, rgba(255, 0, 0, 0.06), rgba(0, 255, 0, 0.02), rgba(0, 0, 255, 0.06))
    z-index: 2
    background-size: 100% 4px, 6px 100%
    pointer-events: none

body
  background-color: $dark
  font-family: 'VT323'
  color: $light
  text-shadow: 0 0 20px $light
